import { getAllAssets, getAssetBySlug } from '@/lib/file-system';
import { VaultFilters } from '@/components/vault/VaultFilters';
import { AssetGallery } from '@/components/vault/AssetGallery';
import { AssetSheet } from '@/components/vault/AssetSheet';
import type { Vertical, ContentType, ContentStatus } from '@/lib/types';

export const dynamic = 'force-dynamic';

interface VaultPageProps {
  searchParams: Promise<{
    verticals?: string;
    types?: string;
    statuses?: string;
    search?: string;
    asset?: string;
  }>;
}

export default async function VaultPage({ searchParams }: VaultPageProps) {
  const params = await searchParams;

  // Parse filters from URL
  const filters = {
    verticals: params.verticals?.split(',').filter(Boolean) as Vertical[] | undefined,
    contentTypes: params.types?.split(',').filter(Boolean) as ContentType[] | undefined,
    statuses: params.statuses?.split(',').filter(Boolean) as ContentStatus[] | undefined,
    search: params.search,
  };

  // Fetch all assets with filters
  const assets = await getAllAssets(filters);

  // Fetch selected asset if any
  const selectedAsset = params.asset
    ? await getAssetBySlug(params.asset)
    : null;

  return (
    <div className="space-y-8">
      {/* Header */}
      <div>
        <h1 className="text-3xl font-bold text-white">Vault</h1>
        <p className="text-white/60 mt-1">
          Asset library and content archive
        </p>
      </div>

      {/* Filters */}
      <VaultFilters
        filters={filters}
        totalCount={assets.length}
      />

      {/* Asset Gallery */}
      <AssetGallery assets={assets} />

      {/* Asset Sheet (Drawer) */}
      {selectedAsset && (
        <AssetSheet asset={selectedAsset} />
      )}
    </div>
  );
}
